@charset 'UTF-8';

// background image base
@mixin background() {
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
}

// image replacement
@mixin ir() {
    text-indent: -9999px;
    overflow: hidden;
}

// absolute center : vertical/horizontal
@mixin abCenter {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

// grid
@mixin grid($width, $divice, $diviceWidth) {
	width: $width;
	float: left;

	@media screen and (max-width: $divice) {
		width: $diviceWidth;
	}
}