@charset 'UTF-8';

/* ===========================================================================
    import webfont
============================================================================= */
// Typography
$font-family-sans: 'Noto Sans KR', sans-serif;
$font-malgun: 'malgun gothic', '맑은 고딕', MalgunGothic, sans-serif;
$font-nanum-gothic: 'Nanum Gothic', sans-serif;
$font-nanum-square: 'NanumSquare', sans-serif;
$font-size-base: 16px;
$line-height-base: 1.3;
$mont: 'Montserrat', sans-serif;

/* proximaNova */
@font-face {
    font-family: 'proximaNova';
    font-style: normal;
    font-weight: 400;
    src: url(../fonts/ProximaNova/ProximaNova-Regular.ttf);
    unicode-range: U+0041-005A, U+0061-007A, U+0030-0039;
}

@font-face {
    font-family: 'proximaNova';
    font-style: normal;
    font-weight: 500;
    src: url(../fonts/ProximaNova/ProximaNova-Medium.ttf);
    unicode-range: U+0041-005A, U+0061-007A, U+0030-0039;
}

@font-face {
    font-family: 'proximaNova';
    font-style: normal;
    font-weight: 700;
    src: url(../fonts/ProximaNova/ProximaNova-Semibold.ttf);
    unicode-range: U+0041-005A, U+0061-007A, U+0030-0039;
}

/* Noto Sans KR */
@font-face {
    font-family: 'Noto Sans KR';
    font-style: normal;
    font-weight: 300;
    src: url(../fonts/Noto_Sans_KR/NotoSans-Light.eot);
    src: url(../fonts/Noto_Sans_KR/NotoSans-Light.eot?#iefix) format('embedded-opentype'),
        url(../fonts/Noto_Sans_KR/NotoSans-Light.woff2) format('woff2'),
        url(../fonts/Noto_Sans_KR/NotoSans-Light.woff) format('woff'),
        url(../fonts/Noto_Sans_KR/NotoSans-Light.otf) format('opentype');
    unicode-range: U+AC00-U+D7A3;
}

@font-face {
    font-family: 'Noto Sans KR';
    font-style: normal;
    font-weight: 350;
    src: url(../fonts/Noto_Sans_KR/NotoSans-DemiLight.eot);
    src: url(../fonts/Noto_Sans_KR/NotoSans-DemiLight.eot?#iefix) format('embedded-opentype'),
        url(../fonts/Noto_Sans_KR/NotoSans-DemiLight.woff2) format('woff2'),
        url(../fonts/Noto_Sans_KR/NotoSans-DemiLight.woff) format('woff'),
        url(../fonts/Noto_Sans_KR/NotoSans-DemiLight.otf) format('opentype');
    unicode-range: U+AC00-U+D7A3;
}

@font-face {
    font-family: 'Noto Sans KR';
    font-style: normal;
    font-weight: 400;
    src: url(../fonts/Noto_Sans_KR/NotoSans-Regular.eot);
    src: url(../fonts/Noto_Sans_KR/NotoSans-Regular.eot?#iefix) format('embedded-opentype'),
        url(../fonts/Noto_Sans_KR/NotoSans-Regular.woff2) format('woff2'),
        url(../fonts/Noto_Sans_KR/NotoSans-Regular.woff) format('woff'),
        url(../fonts/Noto_Sans_KR/NotoSans-Regular.otf) format('opentype');
    unicode-range: U+AC00-U+D7A3;
}

@font-face {
    font-family: 'Noto Sans KR';
    font-style: normal;
    font-weight: 500;
    src: url(../fonts/Noto_Sans_KR/NotoSans-Medium.eot);
    src: url(../fonts/Noto_Sans_KR/NotoSans-Medium.eot?#iefix) format('embedded-opentype'),
        url(../fonts/Noto_Sans_KR/NotoSans-Medium.woff2) format('woff2'),
        url(../fonts/Noto_Sans_KR/NotoSans-Medium.woff) format('woff'),
        url(../fonts/Noto_Sans_KR/NotoSans-Medium.otf) format('opentype');
    unicode-range: U+AC00-U+D7A3;
}

@font-face {
    font-family: 'Noto Sans KR';
    font-style: normal;
    font-weight: 700;
    src: url(../fonts/Noto_Sans_KR/NotoSans-Bold.eot);
    src: url(../fonts/Noto_Sans_KR/NotoSans-Bold.eot?#iefix) format('embedded-opentype'),
        url(../fonts/Noto_Sans_KR/NotoSans-Bold.woff2) format('woff2'),
        url(../fonts/Noto_Sans_KR/NotoSans-Bold.woff) format('woff'),
        url(../fonts/Noto_Sans_KR/NotoSans-Bold.otf) format('opentype');
    unicode-range: U+AC00-U+D7A3;
}

@font-face {
    font-family: 'Noto Sans KR';
    font-style: normal;
    font-weight: 900;
    src: url(../fonts/Noto_Sans_KR/NotoSans-Black.eot);
    src: url(../fonts/Noto_Sans_KR/NotoSans-Black.eot?#iefix) format('embedded-opentype'),
