@charset 'UTF-8';

/*************************************************************
    스트릿패션
**************************************************************/
#streetItem {
    width: 100%;
    padding-top: 5px;
    padding-bottom: 15px;
}

.street-item {
    float: left;
    width: calc(50% - 5px);
    margin-top: 10px;

    img {
        display: inline-block;
        vertical-align: bottom;
    }

    .si_detail-box {
        margin-top: 9px;
        margin-bottom: 10px;
        color: #545454;
        font-size: 13px;
    }
}

.street-img {
    position: relative;
    border-radius: 5px;
    overflow: hidden;

    img {
        width: 100%;
    }

    .item_like-btn {
        position: absolute;
        bottom: 10px;
        right: 10px;
    }
}

.black_circle-box {
    padding: 10px;
    background-color: #000;
    border: 1px solid #333;
    border-radius: 8px;
    box-shadow: 0px -10px 20px 0 rgba(0, 0, 0, 0.3);
}

.float_fc-wrap {
    position: fixed;
    left: 0;
    bottom: 32px;
    width: calc(100% - 64px);
    margin: 0 32px;
}

.float_quick-list {
    li {
        float: left;
        width: 25%;
    }

    .fq-btn {
        display: block;
        width: 100%;
        padding: 1px 0;
        color: #fff;
        font-size: 10px;
        font-weight: 500;
        text-align: center;

        &::before {
            content: '';
            display: block;
            width: 20px;
            height: 20px;
            margin: 0 auto 6px;
        }
    }

    .home::before {
        background: url(../images/icon/float_fc01-icon.png) center center / 100% no-repeat;
    }

    .search::before {
        background: url(../images/icon/float_fc02-icon.png) center center / 100% no-repeat;
    }

    .like::before {
        background: url(../images/icon/float_fc03-icon.png) center center / 100% no-repeat;
    }

    .street::before {
        background: url(../images/icon/float_fc04-icon.png) center center / 100% no-repeat;
    }
}

#floatSearchBox {
    display: none;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
}

#floatTabBar {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
}

.float_search-box {
    position: relative;
    bottom: auto;

    &::before {
        content: '#';
        position: absolute;
        top: 4px;
        left: 10px;
        color: #ff4e25;
        font-size: 17px;
        font-weight: 900;
    }

    .fs_text-input {
        width: 100%;
        height: 35px;
        line-height: 35px;
        padding: 0 35px 0 25px;
        color: #fff;
        background-color: transparent;
        border: 1px solid #ff4e25;
        border-radius: 8px;
        font-size: 16px;

        &::placeholder {
            color: #888;
        }
    }

    .search_gray-btn {
        position: absolute;
        top: 10px;
        right: 10px;
    }

    .search_clear-btn {
        display: none;
        position: absolute;
        top: 10px;
        right: 10px;
    }
}

.fs_hash-box {
    padding-top: 5px;
    overflow: auto;

    .fs_inner {
        -ms-overflow-style: none;
        /* IE and Edge */
        scrollbar-width: none;
        /* Firefox */
    }

    .fs_hash {
        display: inline-block;
        color: #666;
        background-color: transparent;
        padding: 0 12px;
        font-size: 12px;

        &:first-child {
            padding-left: 0;
        }
    }
}

.float_search-wrap {
    position: fixed;
    top: 100%;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: #000;
    z-index: 100;
}

.border_tab-list {
    li {
        float: left;
        text-align: center;
    }

    &.white {
        .bt-btn {
            background-color: #fff;
            border-bottom: 1px solid #dadada;
            
            &.big-font {
                padding: 19px 0 21px;
                font-size: 14px;
            }
        }

        .active .bt-btn {
            color: #ff4e25;
            border-bottom: 2px solid #ff4e25;

            &.big-font {
                padding: 19px 0 20px;
                font-size: 14px;
            }
        }
    }

    .active {
        .bt-btn {
            padding: 20px 0 19px;
            color: #fff;
            border-bottom: 2px solid #ff4e25;

            &.big-font {
                padding: 18px 0 19px;
                font-size: 16px;
            }
        }
    }

    .bt-btn {
        display: block;
        width: 100%;
        line-height: 1;
        padding: 20px 0;
        background-color: #000;
        border-bottom: 1px solid #2a2a2a;
        color: #888;
        font-size: 14px;

        &.big-font {
            padding: 19px 0;
            font-size: 16px;
        }
    }
}

.top_box {
    height: 84px;
    padding: 10px 15px 9px;
    background-color: #121212;
    border-bottom: 1px solid #2a2a2a;
    overflow: hidden;

    .float_search-box {
        width: calc(100% - 48px);

        .search_gray-btn {
            visibility: hidden;
        }
    }

    .fs_close {
        padding: 5px 0 6px 15px;
        background-color: transparent;
        color: #fff;
        font-size: 16px;
        font-weight: 500;
    }

    &.no_search {
        height: auto;
    }
}

.fs-box {
    padding: 20px 15px;
}

.fs-list {
    li {
        margin-top: 15px;
        color: #888;
        font-size: 16px;

        .close_gray-btn {
            margin-top: 3px;
        }
    }

    .active {
        color: #fff;
    }
}

.justify_box {
    &::after {
        content: '';
        display: block;
        clear: both;
    }

    .just_left {
        float: left;
    }

    .just_right {
        float: right;
    }
}

.normal-title {
    margin-bottom: 20px;

    .just_left {
        color: #fff;
        font-size: 12px;
    }
    
    .just_right {
        color: #888;
        font-size: 12px;
        background-color: transparent;
    }
}

.search_none-wrap {
    height: calc(100vh - 121px);
    
    span {
        @include abCenter;
        color: #888;
        font-size: 15px;
    }
}

.search_info-box {
    line-height: 1.5;
    padding: 12px 15px;
    color: #fff;
    border-bottom: 1px solid #2a2a2a;
    font-size: 14px;
    text-align: center;

    &.none {
        padding: 25px 0;
        margin: 0 15px;
        border-bottom: 1px solid #ff4e25;
    }
}

.recc_search-wrap {
    padding-bottom: 36px;
    margin: 0 15px;
    border-bottom: 1px solid #2a2a2a;
}

.wrap-title {
    padding-top: 30px;
    color: #fff;
    font-size: 16px;
}

.hash-list {
    margin-top: 2px;
    margin-left: -20px;

    li {
        float: left;
        height: 14px;
        margin-top: 10px;
        padding-left: 20px;
    }

    .hash-item {
        color: #888;
        background-color: transparent;
        font-size: 14px;
    }
}

#getSearch.sticky {
    padding-top: 55px;

    .top_box {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 100;
    }
}

.photograp_info-wrap {
    padding: 15px;
    
    .pi_logo {
        position: relative;
        display: inline-block;
        width: 30px;
        height: 30px;
        margin-right: 10px;
        border: 1px solid #555555;
        border-radius: 50%;
        text-align: center;

        img {
            @include abCenter();
            max-width: 50%;
        }
    }

    .pi_name {
        display: block;
        line-height: 1;
        color: #f9f9f9;
        font-size: 13px;
        font-weight: 500;
    }
    
    .pi_cate {
        display: block;
        line-height: 11px;
        color: #888;
        margin-top: 6px;
        font-size: 11px;
    }

    .pi_date {
        color: #fff;
        font-size: 14px;
        vertical-align: middle;
    }
}

.photo_detail-img {
    position: relative;

    img {
        width: 100%;
        vertical-align: bottom;
    }

    .sibi_clo-box {
        position: absolute;
        bottom: 15px;
        right: 15px;
        padding: 4px 10px;
        background-color: rgba(0, 0, 0, 0.7);
        border-radius: 30px;
        color: #ff4e25;
        font-size: 15px;
        font-weight: 500;
    }

    .sc_num::before {
        content: '';
        display: inline-block;
        width: 17px;
        height: 14px;
        margin-right: 4px;
        background: url(../images/icon/sibi_clo-icon.png) center center / cover no-repeat;
        vertical-align: -1px;
    }
}

.sibi_item-link {
    display: block;
    position: relative;
    width: 100%;
    height: 40px;
    line-height: 40px;
    padding: 0 15px;
    color: #fff;
    background-color: #243551;
    font-size: 12px;

    &::after {
        content: '';
        display: inline-block;
        position: absolute;
        top: 14px;
        right: 15px;
        width: 8px;
        height: 13px;
        background: url(../images/icon/arrow_white-icon.png) center center / cover no-repeat;
    }
}

.photo_info-wrap {
    padding: 18px 0 32px;
    margin: 0 15px;
    border-bottom: 1px solid #2a2a2a;

    .justify_box {
        margin-bottom: 13px;
    }

    .just_left  {
        &::after {
            content: '';
            display: block;
            clear: both;
        }
    }

    .pi_sns {
        display: block;
        float: left;
        position: relative;
        width: 14px;
        margin-top: 4px;
        margin-right: 18px;

        img {
            width: 100%;
        }

        &::after {
            content: '';
            display: block;
            position: absolute;
            top: 50%;
            right: -10px;
            width: 1px;
            height: 12px;
            background-color: #555;
            transform: translateY(-50%);
        }
    }

    .pi_id {
        display: block;
        float: left;
        color: #f9f9f9;
        font-size: 17px;
        font-weight: 500;
    }

    .pi_rocate {
        vertical-align: middle;
        color: #888;
        font-size: 14px;

        &::before {
            content: '';
            display: inline-block;
            width: 12px;
            height: 15px;
            margin-right: 6px;
            background: url(../images/icon/rocate-icon.png) center center / cover no-repeat;
            vertical-align: -2px;
        }
    }
}

.pi_hash-list {
    margin-bottom: 18px;
    margin-left: -5px;

    li {
        float: left;
        line-height: 1.5;
        margin-left: 5px;
        color: #d9e8f5;
        font-size: 12px;
        font-weight: 300;
    }
}

.pi_btn-box {
    &::after {
        content: '';
        display: block;
        clear: both;
    }
    
    button {
        float: left;
        padding: 4px 8px;
        border-radius: 30px;
    }

    .heart-btn {
        position: relative;
        height: 30px;
        padding: 3px 6px 5px 30px;
        font-size: 14px;

        &::before {
            content: '';
            display: inline-block;
            position: absolute;
            top: 7px;
            left: 8px;
            width: 17px;
            height: 15px;
            margin-right: 4px;
            background: url(../images/icon/like-icon.png) center center / cover no-repeat;
            vertical-align: -4px;
        }

        &.active::before {
            background: url(../images/icon/like_active-icon.png) center center / cover no-repeat;
        }
    }

    .share-btn {
        padding: 0;
        width: 30px;
        height: 30px;
        margin-left: 10px;
        background-position: 4px center;
    }
}

.slide-popup {
    @include abCenter();
    width: 100%;

    .sp_title {
        margin-bottom: 30px;
        color: #f9f9f9;
        font-size: 16px;
        text-align: center;
    }

    .pop_close-btn {
        display: block;
        margin: 30px auto 0;
    }
}

.item_slide-wrap {
    .is-item {
        margin: 0 10px;
    }

    .is_img {
        width: 100%;
        height: 290px;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
    }
}

.item_info-box {
    width: 100%;
    padding: 20px;
    background-color: #fff;

    .item_brand {
        display: block;
        color: #333;
        font-size: 13px;
        font-weight: 700;
    }

    .item_title {
        line-height: 1.3;
        margin-bottom: 10px;
        color: #555;
        font-size: 12px;
    }

    .item_pay {
        font-size: 14px;
        color: #333;
        font-weight: 700;
    }

    .beforesale {
        color: #bbb;
        font-size: 12px;
        font-weight: 400;
        text-decoration: line-through;
    }
}

.like_toggle-box {
    margin-top: 10px;
    color: #888;
    font-size: 12px;

    .like_toggle-btn {
        margin-right: 4px;
        vertical-align: -2px;
    }
}

.center_slide-wrap {
    .is-item {
        min-height: 422px;
        margin: 0 10px;

        img {
            width: 100%;
            margin: 0 auto;
            border: 1px solid #555;
        }
    }
}

.street_photo_info-wrap {
    padding: 30px 15px;
    border-bottom: 1px solid #2a2a2a;

    .d-table {
        width: 100%;
        margin-bottom: 15px;
    }

    .d-table_cell {
        vertical-align: middle;

        &:first-child {
            width: 95px;
        }
    }

    .spi-img {
        width: 80px;
        height: 80px;
        border: 1px solid #555;
        border-radius: 50%;
        overflow: hidden;
    }
    
    .spi-name {
        display: block;
        line-height: 1;
        margin-bottom: 8px;
        color: #fff;
        font-size: 18px;
        font-weight: 500;
    }

    .spi-text {
        color: #888;
        font-size: 12px;
        line-height: 1.5;
    }
}

.border_right-list {
    &::after {
        content: '';
        display: block;
        clear: both;
    }

    li {
        float: left;
        border-right: 1px solid #333;

        &:last-child {
            border-right: none;
        }
    }
}

.num_info-wrap {
    padding: 20px 0;
    color: #888;
    background-color: #111;
    border-radius: 5px;
    font-size: 12px;

    .ni_num {
        display: inline-block;
        width: 40px;
        margin-left: 15px;
        color: #fff;
        font-size: 20px;
        vertical-align: -2px;
        text-align: center;
    }

    .border_right-list {
        li {
            width: 50%;
            text-align: center;
        }
    }
}

.ph_icon-list {
    &::after {
        content: '';
        display: block;
        clear: both;
    }

    li {
        float: left;
        margin-left: 22px;
    }

    .ph-btn {
        display: block;
    }

    .ph-btn.home {
        width: 22px;
        height: 21px;
        background: url(../images/icon/home-icon.png) center center / cover no-repeat;
    }

    .ph-btn.cart {
        width: 21px;
        height: 21px;
        background: url(../images/icon/cart-icon.png) center center / cover no-repeat;
    }

    .ph-btn.search {
        width: 21px;
        height: 21px;
        background: url(../images/icon/search-icon.png) center center / cover no-repeat;
    }
}

.my_info-wrap {
    margin-bottom: 15px;
    background-color: #090909;
}

.black_border_tab-list {
    &::after {
        content: '';
        display: block;
        clear: both;
    }

    li {
        float: left;

        &:first-child .bt-btn {
            border-left: none;
        }

        &:last-child .bt-btn {
            border-right: none;
        }
    }

    .active {
        .bt-btn {
            border: 1px solid #111;
            border-bottom: 1px solid transparent;
        }
    }

    .bt-btn {
        width: 100%;
        padding: 20px 0;
        color: #111;
        background-color: transparent;
        border: 1px solid #e5e5e5;
        border-bottom: 1px solid #111;
        border-right: none;
        font-size: 14px;
        font-weight: 500;
    }
}

#topBtn {
    display: none;
    width: 40px;
    height: 40px;
    margin: 0 0 100px auto;
    background: url(../images/icon/top-icon.png) center center / cover no-repeat;
    transition: all 0.2s;

    &.active {
        transform: translateY(-30px);
    }
}

.member-container {
    width: 100%;
    max-width: 466px;
    margin: 0 auto;
}

.member-wrap {
    padding: 40px 30px;

    &.no-top {
        padding: 0 30px 40px 30px;
    }
}

.register-title {
    line-height: 1.3;
    margin-bottom: 38px;
    font-size: 30px;
    font-weight: 700;
    letter-spacing: -1.21px;

    &.text-center {
        text-align: center;
    }
}

.member-label {
    display: block;
    line-height: 1;
    margin-bottom: 10px;
    font-size: 13px;
    font-weight: 500;
    letter-spacing: -0.52px;
}

.input-box {
    position: relative;
    margin-bottom: 36px;
}

.text-input {
    width: 100%;
    height: 45px;
    line-height: 43px;
    padding: 0 10px;
    color: #000;
    border: 1px solid #e5e5e5;
    font-size: 16px;
    
    &::placeholder {
        line-height: 43px;
        font-size: 13px;
        color: #ababab;
    }

    &.error {
        border: 1px solid #f43434;
    }

    &:focus {
        outline: none;
    }
}

.error-msg {
    position: absolute;
    bottom: -3px;
    display: block;
    line-height: 1.3;
    color: #f43434;
    font-size: 11px;
    letter-spacing: -0.44px;
    transform: translateY(100%);
}

.sz-custom_text {
    margin-left: 7px;
    color: #444;
    font-size: 12px;
    vertical-align: 8px;
}

.radio-check {
    .sz-custom_text {
        color: #333;
        font-size: 14px;
        vertical-align: 1px;
    }
}

.agree-acco:last-child {
    .ac-content {
        padding: 10px 0 0 35px;
    }
}

.ac-title {
    position: relative;

    .sz-custom_label {
        vertical-align: bottom;
    }

    .sz-custom_text {
        vertical-align: 3px;
    }

    .acco-button {
        content: '';
        display: block;
        position: absolute;
        top: 0px;
        right: 0;
        width: 24px;
        height: 24px;
        background: url(../images/icon/acco_arrow-icon.png) right center / 13px 7px no-repeat;

        &.active {
            transform: rotate(180deg);
            background: url(../images/icon/acco_arrow-icon.png) left center / 13px 7px no-repeat;
        }
    }
}

.ac-content {
    display: none;
    padding: 10px 0 10px 35px;

    .sz-custom_label {
        vertical-align: bottom;
    }

    .sz-custom_text {
        vertical-align: 3px;
    }
}

.login_fc-list {
    margin-top: 10px;
    text-align: center;

    li {
        display: inline-block;

        &:last-child a::after {
            display: none;
        }
    }
    
    a {
        color: #444;
        font-size: 12px;

        &::after {
            content: '';
            display: inline-block;
            width: 1px;
            height: 12px;
            margin: 0 18px;
            background-color: #dadada;
            vertical-align: -2px;
        }
    }
}

.line-btn {
    width: 100%;
    height: 45px;
    padding: 0 10px;
    margin-bottom: 5px;
    background-color: #fff;
    border: 1px solid #e5e5e5;
    color: #444;
    font-size: 12px;
    text-align: left;

    &:last-child {
        margin-bottom: 0;
    }

    &::before {
        content: '';
        display: inline-block;
        width: 25px;
        height: 25px;
        margin-right: 10px;
        vertical-align: -8px;
    }

    &.naver::before {
        background: url(../images/icon/naver-icon.png) center center / cover no-repeat;
    }

    &.kakao::before {
        background: url(../images/icon/kakao-icon.png) center center / cover no-repeat;
    }

    &.facebook::before {
        background: url(../images/icon/facebook-icon.png) center center / cover no-repeat;
    }
}

.member-sepa {
    width: calc(100% - 30px);
    height: 1px;
    margin: 0 15px;
    background-color: #e5e5e5;
    border: none;
}

.info-text {
    color: #444;
    font-size: 12px;
    letter-spacing: -0.24px;
    text-align: center;
}

.id_find-text {
    color: #000;
    font-size: 17px;
    letter-spacing: -0.68px;
    text-align: center;

    .orange {
        color: #ff4e25;
        font-size: 20px;
        font-weight: 700;
        letter-spacing: -0.8px;
    }
}

.terms-content {
    .terms-text {
        color: #898989;
        font-size: 13px;
        font-weight: 700;
    }
}

.sns-title {
    font-size: 15px;
    font-weight: 500;
}

.phone-first {
    width: calc(30% - 5px);
    margin-right: 5px;
}

.phone-last {
    width: 70%;
}

.item_input-box {
    position: relative;

    .item-input {
        padding: 0 55px 0 10px;
    }

    .ii-item {
        position: absolute;
        top: 10px;
        right: 10px;
        font-size: 15px;
        letter-spacing: -0.63px;
    }
}

.mr-list {
    margin-bottom: 25px;

    li {
        position: relative;
        float: left;
        line-height: 1.3;
        width: 33.33%;
        padding: 0 13px;
        font-size: 11px;
        font-weight: 500;
        letter-spacing: -0.66px;
        text-align: center;

        img {
            height: 36px;
        }

        p {
            margin-top: 10px;
        }

        &::after {
            content: '';
            display: block;
            position: absolute;
            top: 50%;
            right: 0;
            width: 1px;
            height: 25px;
            background-color: #e5e5e5;
            transform: translateY(-50%);
        }

        .big {
            font-size: 12px;
        }
    }

    li:last-child::after {
        display: none;
    }
}