@charset 'UTF-8';

/* ************************************************
    common class
************************************************* */
.blind {
    visibility: hidden;
    overflow: hidden;
    width: 0;
    height: 0;
    font-size: 0;
    line-height: 0;
}

.clearfix::after {
    content: '';
    display: block;
    clear: both;
}

.po-re {
    position: relative;
}

body.scrollOff {
    height:100%;
    min-height:100%;
    overflow:hidden !important;
    touch-action:none;
}

/* ************************************************
    common class
************************************************* */
.sz-wrap {
    width: 100%;
    padding: 0 15px;
}

.mo-wrap {
    width: 100%;
    min-height: 100vh;
    margin: 0 auto;
    background-color: #000;
    
    &.white {        
        background-color: #fff;
    }
}

.d-table {
    display: table;
}

.d-table_cell {
    display: table-cell;
}

.orange {
    color: #ff4e25 !important;
}

.text_center {
    text-align: center;
}

.f-left {
    float: left;
}

.f-right {
    float: right;
}

.margin_b-5 {
    margin-bottom: 5px;
}

.margin_b-10 {
    margin-bottom: 10px;
}

.margin_b-15{
    margin-bottom: 15px;
}

.margin_b-20 {
    margin-bottom: 20px;
}

.margin_b-25 {
    margin-bottom: 25px;
}

.margin_b-40 {
    margin-bottom: 25px;
}

/* ************************************************
    header
************************************************* */
.page-header {
    padding: 13px 0 9px;
    background-color: #111;
    border-bottom: 1px solid #2a2a2a;

    .page-title {
        display: inline-block;
        margin-left: 25px;
        color: #fff;
        font-size: 16px;
        font-weight: 700;
        vertical-align: 4px;
    }

    &.white {
        background-color: #fff;
        border-bottom: 1px solid #e5e5e5;

        .page-title {
            color: #111;
        }

        .back-btn {
            background: url(../images/icon/back_arrow_black-icon.png) center center / cover no-repeat;
        }
    }

    &.no_border {
        border-bottom: none;
    }
}

/* ************************************************
    btn
************************************************* */
[class$="-btn"] {
    display: inline-block;
    text-align: center;
}

.share-btn {
    background: url(../images/icon/share-icon.png) center center / auto no-repeat;
    background-size: 17px 17px;
}

.back-btn {
    width: 11px;
    height: 22px;
    background: url(../images/icon/back_arrow-icon.png) center center / cover no-repeat;
}

.search_gray-btn {
    display: block;
    width: 15px;
    height: 15px;
    padding: 0;
    background: url(../images/icon/search_gray-icon.png) center center / cover no-repeat;
    @include ir();
}

.close_gray-btn {
    width: 10px;
    height: 10px;
    background: url(../images/icon/close_gray-icon.png) center center / cover no-repeat;
}

.like_toggle-btn {
    width: 17px;
    height: 15px;
    background: url(../images/icon/like_toggle-icon.png) center center / cover no-repeat;
}

.pop_close-btn {
    width: 30px;
    height: 30px;
    background: url(../images/icon/pop_close-btn.png) center center / cover no-repeat;
}

.delete-btn {
    width: 15px;
    height: 15px;
    background: url(../images/icon/delete-icon.png) center center / cover no-repeat;
}

.item_like-btn {
    width: 30px;
    height: 30px;
    background: url(../images/icon/item_like-icon.png) center center / cover no-repeat;

    &.active {
        background: url(../images/icon/item_like_active-icon.png) center center / cover no-repeat;
    }
}

.search_clear-btn {
    width: 15px;
    height: 15px;
    padding: 0;
    background: url(../images/icon/delete_gray-icon.png) center center / cover no-repeat;
}

.darkgray-btn {
    color: #fff;
    background-color: #2a2a2a;
    border: 1px solid #2a2a2a;
}

.gray-btn {
    color: #fff;
    background-color: #5d5e6a;
    border: 1px solid #5d5e6a;
}

.medium-btn {
    width: 100%;
    height: 45px;
    line-height: 43px;
    font-size: 13px;
    font-weight: 500;
}

.large-btn {
    width: 100%;
    height: 55px;
    line-height: 53px;
    font-size: 18px;
    font-weight: 500;
}

.stop-btn {
    background-color: #b6b7bf;
    border: 1px solid #b6b7bf;
    color: #e5e6f0;
}

.text-btn {
    color: #ababab;
    background-color: transparent;
    font-size: 12px;
}

.white-btn {
    color: #444;
    background-color: #fff;
    border: 1px solid #444;
}

.black-btn {
    color: #fff;
    background-color: #000000;
    border: 1px solid #000000;
}

/* ************************************************
    popup
************************************************* */
.popup-wrap {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 100;
}

.popup-layer {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
}

.popup-content {
    .page-header {
        padding: 15px 0 9px;

        .sz-wrap {
            padding: 0 20px;
        }
    }

    .page-title {
        font-size: 18px;
        margin-left: 0;
    }
}

.popup_close-btn {
    position: fixed;
    left: 50%;
    bottom: 30px;
    width: 40px;
    height: 40px;
    background: url(../images/icon/popup_close-icon.png) center center/cover no-repeat;
    transform: translateX(-50%);
}

.terms-popup {
    width: 100%;
    height: 100vh;
    background-color: #fff;

    .terms-content {
        margin: 0 auto;
        max-width: 466px;
        padding: 25px 20px 95px;
        height: calc(100vh - 50px);
        overflow: auto;
    }
}

/* ************************************************
    select
************************************************* */
.member-wrap {
    .sz-select_list .selected {
        padding: 13px 10px 14px;
        border: 1px solid #e5e5e5;
        background: url(../images/icon/acco_arrow-icon.png) 89% center / 13px 7px no-repeat;
        font-size: 16px;
        cursor: pointer;

        &::after {
            content: '';
            display: block;
        }
    }

    .sz-select_sub {
        border: 1px solid #e5e5e5;
        border-top: none;
        background-color: #fff;

        li {
            padding: 10px;
            font-size: 16px;
        }
    }
}